import classes from "./main.module.css";

const obj = [
  {
    owner: "Mahmoud Bakr",
    model: "jsx1",
    photo: "/harley.jpg",
    price: "343900 $",
  },
  {
    owner: "Mostafa Mohamed",
    model: "jsx1",
    photo: "/bmw.jpg",
    price: "43200 $",
  },
];
export default function Main({ setColor }) {
  return (
    <div className={classes.container}>
      
        <article className={classes.motorContainer}>
          <label className={classes.border} htmlFor="setArticleColor">
            <input
              name="--article-color"
              onChange={setColor}
              style={{ display: "none" }}
              type="color"
              id="setArticleColor"
            ></input>

            <header>
              <span>{obj[0].owner}</span>
            </header>
          </label>
          <main>
            <img src={obj[0].photo} alt=""></img>
          </main>
          <footer>
            <h2>price: {obj[0].price}</h2>
            <h2>model: {obj[0].model}</h2>
          </footer>
        </article>
        <article className={classes.motorContainer}>
          <label className={classes.border} htmlFor="setArticleColor">
            <input
              name="--article-color"
              onChange={setColor}
              style={{ display: "none" }}
              type="color"
              id="setArticleColor"
            ></input>

            <header>
              <span>{obj[1].owner}</span>
            </header>
          </label>
          <main>
            <img src={obj[1].photo} alt=""></img>
          </main>
          <footer>
            <h2>price: {obj[1].price}</h2>
            <h2>model: {obj[1].model}</h2>
          </footer>
        </article>

    </div>
  );
}
