import {Fragment ,useState} from "react";
import {Switch ,Route} from "react-router-dom";

import classes from "./home.module.css";
import SideBar from "../../Components/SideSar/sidebar";
import Main from "../../Components/main/main";

let langs ={
    ar :{
        Home:'الرئيسه',
        Myinterests:"اهتماماتي",
        AddMotor:"اعرض دراجتك"
    
    },
    en:{
        Home:'Home',
        Myinterests:"My interests",
        AddMotor:"Sell your motor"
    }
}

function Home (){  
    const [lang,setLang] =useState(langs.en)
    const setColor =(e)=>{
           const color = e.target.value
            const red = parseInt(color.substr(1,2), 16)
            const g = parseInt(color.substr(3,2), 16)
            const b = parseInt(color.substr(5,2), 16)
          
        let r = document.querySelector(':root');
        r.style.setProperty(e.target.name, `rgba(${red},${g},${b},.9)`);
    }

    
        return(
            <Fragment>
                <Switch>

                <div className={classes.container}>

                    <div  className={classes.body}>
                        <SideBar lang={lang} setColor={setColor}/>
                        
                        <div>
                        <label className={classes.border} htmlFor='setBorderColor'>
                        <input name='--border-color' onChange={setColor} style={{display:'none'}} type='color' id='setBorderColor'></input>
                        </label>
                        </div>

                        <label className={classes.container2} htmlFor='setBodyColor'>
                        <input name='--body-color' onChange={setColor} style={{display:'none'}} type='color' id='setBodyColor'></input>
            
                            <Route exact  path='/'>
                                <Main setColor={setColor}/>
                            </Route>
                        
                        </label>
                        <footer style={{backgroundColor:'var(--nav-color)',display:'flex' ,justifyContent:'space-around'}}>
                            <h1 style={{cursor:'pointer'}} onClick={()=>{setLang(langs.ar)}}>Arabic</h1>
                            <h1 style={{cursor:'pointer'}} onClick={()=>{
                                
                                setLang(langs.en)
                                
                                }}>English</h1>

                        </footer>
                     </div>

            </div>
                    {/* <Route  path='/'><Redirect to='/' /></Route> */}
                    </Switch>
                    
            </Fragment>

        )

    

}

export default Home