import {useState} from "react";
import SidebarItem from "./sidebar_item/sidebarItem";

import classes from "./sidebar.module.css";

import motorcycle from "./icons/motorcycle.png"
import deal from "./icons/deal.png"
import garage from "./icons/garage.png"
import home from "./icons/home.png"

function Sidebar({setColor ,lang}) {
    let [activeItem ,set_activeItem] = useState(window.location.pathname) 
    
    
    
    return (
        <div className={classes.mainContainer}>
            <label className={classes.container} htmlFor='setNavColor'>
            <input name='--nav-color' onChange={setColor} style={{display:'none'}} type='color' id='setNavColor'></input>
            <h2 className={classes.logo} >
            <label htmlFor='setActiveColor' className={classes.icon} style={{WebkitMask:`url(${motorcycle}) no-repeat center`,}}>
            <input name='--active-color' onChange={setColor} style={{display:'none'}} type='color' id='setActiveColor'></input>
            </label>
            <label htmlFor='setFontColor'>
                my Motor Cycle
            </label>
            <input name='--font-color' onChange={setColor} style={{display:'none'}} type='color' id='setFontColor'></input>
            </h2>
            <ul  className={classes.sidebar_list}>
                <SidebarItem name={lang.Home} path='/' 
                    setActive ={set_activeItem} theActiveItem={activeItem}
                    icon ={home} 
                    />
                
                <SidebarItem name={lang.Myinterests} path='/MyInterests' 
                setActive ={set_activeItem} theActiveItem={activeItem}
                icon = {garage} 
                />
            
                <SidebarItem name={lang.AddMotor} path='/AddMotor' 
                setActive ={set_activeItem} theActiveItem={activeItem}
                icon = {deal}
                /> 
            
            
            </ul>

                </label>
        </div>
    )
}

export default Sidebar 